@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}

body {
	
	min-height: 100vh;
	background-color: #d3d3d3;
}

body::-webkit-scrollbar {
	width: 15px;
  height: 50px;
}

body::-webkit-scrollbar-thumb {
	background-color: #0381ffaf;
	border-radius: 50px;
  height: 50px;
}

body::-webkit-scrollbar-track {
	background-color: #a4acc4;
}

.coin-app {
	display: flex;
	flex-direction: column;
	align-items: center;
	min-height: 100vh;
}

.coin-search {
	margin-bottom: 4rem;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: center;
}

.coin-text {
	margin-bottom: 2rem;
	text-align: center;
  display: flex;
  width: 100%;
  padding: 1rem;
  color: gray;
}

.coin-input {
	padding-left: 1rem;
  font-size: 1.2rem;
	width: 300px;
	height: 50px;
	border-radius: 30px;
	border: none;
	background-image: linear-gradient(
		-225deg,
		#32e4db 0%,
		#18d1f2 50%,
		#0199ff 100%
	);
	color: white
}

.coin-input::-webkit-input-placeholder {
	color: white;
  
}

.coin-input:-ms-input-placeholder {
	color: white;
  
}

.coin-input::placeholder {
	color: white;
  
}
.coin-input:focus{
	outline: none;
  
}

.coin-content {
	display: flex;
	flex-direction: column;
	align-items: center;
  height: 100%;
}

.coin-container {
	display: flex;
	justify-content: center;
	height: 100%;
}

.coin-row {
	display: flex;
	flex-direction: column;
	justify-items: center;
	align-items: center;
	/* height: 60vh; */
	border-bottom: 1px solid #2e344e;
	border-top: 1px solid #2e344e;
	border-left: 1px solid #2e344e;
	border-radius: 1rem 1rem 1rem 1rem;
	width: 80vw;
	margin-bottom: 10px;
	box-shadow: 2px 3px rgba(50, 50, 50, 0.5);
	background-color: #31394d;
	height: 100%;
}

hr {
	border-color: #7c8097;
	width: 90%;
	margin-top: 24px;
	margin-bottom: 24px;
}

.coin {
	display: flex;
	align-items: center;
	padding-right: 24px;
	min-width: 300px;
}

.coin-title {
	margin-top: 0.5rem;
	border: none;
	padding: 0.5rem;
	color: #ffffff;
	font-size: 1.6rem;
	font-weight: 300;
	/* 	
	border-radius: 30px;
	box-shadow: 0 13px 26px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.16); */
	text-align: center;
}

span {
	font-size: 1.2rem;
	color: gray;
}

.color-a {
	background: linear-gradient(to top right, #8162ce, #f54ba5);
}
.color-b {
	background: linear-gradient(to bottom right, #c90a6d, #ff48a0);
}

.color-c {
	background: linear-gradient(to bottom right, #5e5aec, #3f9efc);
}
.color-d {
	background: linear-gradient(to bottom right, #6452e9, #639ff9);
}

.coin h1 {
	font-size: 2.4rem;
	width: 150px;
	color: white;
}

.coin img {
	width: 5rem;
	margin: 1.2rem auto;
}

.coin-symbol {
	text-transform: uppercase;
	font-size: 1.5rem;
	color: gray;
}

.coin-data {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-around;
	width: 100%;
	height: 100%;
}

.coin-data-box {
	width: 27%;
	height: 200px;
	margin: 1rem;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-radius: 30px;
	box-shadow: 0 13px 26px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.16);
}

.coin-price {
	border-radius: 10%;
	border: none;
	height: 80px;
	width: 80px;
	color: #ffffff;
	box-shadow: 0 13px 26px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.16);
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 1rem;
	margin-bottom: 2rem;
}

.coin-volume {
	width: 200px;
}

.coin-marketcap {
	width: 200px;
}

@media screen and (max-width: 400px) {
	.coin-data-box {
		width: 70% !important;
        
	}
	.coin-marketcap {
		width: 150px;
	}
	.coin-volume {
		width: 150px;
	}

    .coin h1{
        font-size: 2rem;
    }
    .coin-input{
        width: 250px;
    }
}
@media screen and (max-width: 900px) {
	.coin-data-box {
		width: 40%;
        
	}
}

